import { call, put } from 'redux-saga/effects';
import axios from 'axios';
import { setProfile, loadingFailed, startLoading } from 'src/pages/slice/profileSlice';

//   
interface ProfileResponse {
    id: string;
    email: string;
    signUpDate: string;
}

//       
const getTokenFromLocalStorage = () => {
    return localStorage.getItem('token');
};

//      API
const fetchProfile = async (token: string) => {
    const response = await axios.get('http://19429ba06ff2.vps.myjino.ru/api/profile', {
        headers: { Authorization: `Bearer ${token}` }, //    
    });
    return response.data;
};

//    
function* profileSaga() {
    try {
        const token = getTokenFromLocalStorage(); //     

        console.log('token:', token);

        if (!token) {
            throw new Error('  '); //    ,  
        }

        //yield put(startLoading()); //     true
        const data: ProfileResponse = yield call(fetchProfile, token); //    API  
        yield put(setProfile(data)); //   ,     Redux
    } catch (error) {
        yield put(loadingFailed(error.message || '   ')); //   ,    
    }
}

export default profileSaga;