import { createSlice, PayloadAction } from '@reduxjs/toolkit';

//   
interface ProfileData {
    id: string;
    email: string;
    signUpDate: string;
}

//   
interface ProfileState {
    data: ProfileData | null;
    isLoading: boolean;
    error: string | null;
}

const initialState: ProfileState = {
    data: null,
    isLoading: false,
    error: null,
};

//  
const profileSlice = createSlice({
    name: 'profile',
    initialState,
    reducers: {
        //     
        setProfile: (state, action: PayloadAction<ProfileData>) => {
            state.data = action.payload;
            state.isLoading = false;
            state.error = null;
            console.log('  :', action.payload);
        },
        //    
        clearProfile: (state) => {
            state.data = null;
            state.isLoading = false;
            state.error = null;
        },
        //    
        startLoading: (state) => {
            state.isLoading = true;
            state.error = null;
            console.log('startLoading profileSlice');
        },
        //    
        loadingFailed: (state, action: PayloadAction<string>) => {
            state.isLoading = false;
            state.error = action.payload;
        },
    },
});

export const { setProfile, clearProfile, startLoading, loadingFailed } = profileSlice.actions;

export default profileSlice.reducer;