import React, {useEffect, useState } from 'react';
import { useForm, SubmitHandler } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { startLogin } from 'src/features/auth/authSlice';
import { RootState } from 'src/app/providers/StoreProvider';
import './css/loginForm.css'
import Loader from "src/shared/components/Loader";

interface LoginFormInputs {
  email: string;
  password: string;
  role: string;
}

export const LoginForm: React.FC = () => {
  const { register, handleSubmit, formState: { errors } } = useForm<LoginFormInputs>();
  const dispatch = useDispatch();

  //     
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);

  //    Redux
  const { token, isLoading, error } = useSelector((state: RootState) => state.auth);

  const onSubmit: SubmitHandler<LoginFormInputs> = (data) => {
    setIsModalOpen(true); //     
    dispatch(startLogin(data)); //    
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  const handleModalRetry = () => {
    setIsModalOpen(true); //     
  };

  //      ,   
  useEffect(() => {
    if (token) {
      setIsModalOpen(false);
    }
  }, [token]);

  return (
      <div className="login-form-container">
        <form onSubmit={handleSubmit(onSubmit)} className="login-form">
          <div>
            <label>Email:</label>
            <input
                type="email"
                {...register('email', {
                  required: 'Email ',
                  pattern: {
                    value: /^\S+@\S+$/i,
                    message: ' email',
                  },
                })}
            />
            {errors.email && <p>{errors.email.message}</p>}
          </div>

          <div>
            <label>:</label>
            <input
                type="password"
                {...register('password', {
                  required: ' ',
                  minLength: {
                    value: 6,
                    message: '   6 ',
                  },
                })}
            />
            {errors.password && <p>{errors.password.message}</p>}
          </div>

          <div>
            <label>:</label>
            <select
                {...register('role', {
                  required: '  ',
                })}
            >
              <option value=""> </option>
              <option value="user"></option>
              <option value="admin"></option>
            </select>
            {errors.role && <p>{errors.role.message}</p>}
          </div>


          <button onClick={handleCloseModal} type="submit" disabled={isLoading}></button>
        </form>

        {isModalOpen && (
            <div>
              {isLoading ? (
                  <Loader />
              ) : error ? (
                  <div>
                    <p>{error}</p>
                    {/*<button onClick={handleModalRetry}> </button>*/}
                  </div>
              ) : (
                  <div>
                    <p> !</p>
                    <button onClick={handleCloseModal}></button>
                  </div>
              )}
            </div>
        )}
      </div>
  );
};

export default LoginForm;